* {
  font-family: "Poppins", sans-serif;
  padding: 0;
  margin: 0;
  outline: none;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #e3f2fd;
}

.btn {
  font-size: 1rem;
  font-weight: 500;
  color: #ffffff;
  border: none;
  padding: 0.5rem 3.5rem;
}

.btn-left {
  border-radius: 0.2rem 0 0 0.2rem;
  background-color: #5e35b1;
}

.btn-right {
  border-radius: 0 0.2rem 0.2rem 0;
  background-color: #1e88e5;
}

.card {
  width: 20rem;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  border-radius: 0.4rem;
  padding: 1rem;
  margin-top: 1.5rem;
}

input {
  font-size: 0.9rem;
  font-weight: 400;
  background-color: #ffffff;
  border: 2px solid #ccc;
  border-radius: 0.3rem;
  padding: 0.5rem 0.9rem;
}

.submit-btn {
  border-radius: 0.2rem;
  background-color: #5e35b1;
  margin-top: 1rem;
}

.content {
  width: 26rem;
  margin-top: 2rem;
  border: 2px dashed #8865C7;
  padding: 1rem 1rem;
  border-radius: 0.5rem;
}

label {
  font-size: 1.1rem;
  font-weight: 500;
  color: #020202;
  /* display: inline-block;
  width: 150px; */
}

p {
  font-size: 1rem;
  font-weight: 500;
  margin-top: 0.5rem;
  white-space: pre-wrap;
  word-wrap: break-word; 
}

.form-label {
  display: inline-block;
  width: 90px;
}

.form-div {
  margin-bottom: 10px;
}

.form-input {
  padding: 5px 10px;
}


.error-message {
  background-color: #fce4e4;
  border: 1px solid #fcc2c3;
  float: left;
  padding: 6px 09px;
}

.error-text {
  color: #cc0033;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 20px;
  text-shadow: 1px 1px rgba(250,250,250,.3);
}

.ok-message {
  background-color: #bfecad;
  border: 1px solid #bfecad;
  float: left;
  padding: 6px 09px;
}

.ok-text {
  color: #039645;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 20px;
  text-shadow: 1px 1px rgba(250,250,250,.3);
}


